/* eslint-disable no-console */
import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login/Login'
import Dashboard from '@/components/Dashboard/Dashboard'
import CreateList from '@/components/List/Form/FormList'
import List from '@/components/List/List/List'
import UserForm from '@/components/UserForm/UserForm'
import ChargeList from '@/components/Charge/List/Charge'
import ChargeForm from '@/components/Charge/Form/ChargeForm'
import DashboardLayout from '@/components/Layouts/dashboard/DashboardLayout'
import RemoteAgentsList from '@/components/RemoteAgents/List/RemoteAgentsList'
import RemoteAgentsForm from '@/components/RemoteAgents/Form/RemoteAgentsForm'
import CampaignList from '@/components/Campaign/List/CampaignList'

import axios from 'axios'
import { API_URL } from '../config'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Login',
            component: Login,
            meta: {
                title: 'Login'
            }
        },
        {
            path: '/dashboard',
            component: DashboardLayout,
            children: [
                {
                    path: '/dashboard',
                    name: 'Dashboard',
                    component: Dashboard,
                    meta: {
                        title: 'Dashboard'
                    }
                },
                {
                    path: '/charge',
                    name: 'ChargeList',
                    component: ChargeList,
                    meta: {
                        title: 'Cargas'
                    }
                },
                {
                    path: '/charge-form',
                    name: 'ChargeForm',
                    component: ChargeForm,
                    meta: {
                        title: 'Subir lista'
                    }
                },
                {
                    path: '/lists',
                    name: 'List',
                    component: List,
                    meta: {
                        title: 'Listas'
                    }
                },
                {
                    path: '/create-list',
                    name: 'ListForm',
                    component: CreateList,
                    meta: {
                        title: 'Crear lista'
                    }
                },
                {
                    path: '/remote-agents-list',
                    name: 'RemoteAgentsList',
                    component: RemoteAgentsList,
                    meta: {
                        title: 'Lista de agentes'
                    }
                },
                {
                    path: '/remote-agents-form',
                    name: 'RemoteAgentsForm',
                    component: RemoteAgentsForm,
                    meta: {
                        title: 'Crear'
                    }
                },
                {
                    path: '/campaign-list',
                    name: 'CampaignList',
                    component: CampaignList,
                    meta: {
                        title: 'Lista de campañas'
                    }
                },
                {
                    path: '/user-form',
                    name: 'UserForm',
                    component: UserForm,
                    meta: {
                        title: 'Formulario de usuario'
                    }
                }
            ]
        },
        {
            path: '*',
            redirect: '/'
        }
    ]
})

router.beforeEach((to, from, next) => {
    document.title = to.meta.title || 'Rept'

    let def = true
    
    if(to.name != 'Login'){
        def = '/login'
    }

    axios.post(`${API_URL}/validate`, {}, {
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('access_token')
        }
    }).then((res) => {
        if(res.data.status){
            if(to.name != 'Login'){
                next()
            }else{
                document.title = 'Dashboard'
                next({
                    path : '/dashboard'
                })
            }
        }
    }).catch(() => {
        next({
            path: def
        })
    })
})

export default router