/* eslint-disable no-console */
import { API_URL } from "./../../../config"

export default {
    name: "DashboardLayout",
    data() {
        return {
            drawer: false,
            headerState: true,
            config: '',
            miniDrawState: true,
            actives: {
                dashActive: true,
                userFormActive: false,
                chargeActive: false,
                listActive: false,
                remoteAgentsActive: false,
                campaignActive: false
            }
        }
    },
    mounted: function () {
        this.routeElements(this.$router.currentRoute.name)

        this.config = {
            headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access_token')
            }
        }
    },
    methods: {
        logout() {
            this.axios.post(`${API_URL}/logout`, {}, this.config)
            .then(res => {
                if(res.data.status) {
                    this.$q.notify({
                        position: 'top',
                        color: 'blue-grey-7',
                        message: 'Se cerró la sesión',
                        timeout: 2000,
                    })
                }
            })

            localStorage.removeItem('access_token')
            this.$router.push({name:'Login'})
        },
        route(name) {
            if((name != 'Dashboard' && this.actives.dashActive) || (name != 'UserForm' && this.actives.userFormActive) ||
                (name != 'List' && this.actives.listActive) || (name != 'ChargeList' && this.actives.chargeActive) ||
                (name != 'RemoteAgentsList' && this.actives.remoteAgentsActive) || (name != 'CampaignList' && this.actives.campaignActive)){
                this.routeElements(name)
                this.$router.push({name: name})
            }
        },
        routeElements(name){
            switch(name){
                case 'Dashboard':
                    this.selectModule('dashActive')
                    break
                case 'UserForm':
                    this.selectModule('userFormActive')
                    break
                case 'List':
                    this.selectModule('listActive')
                    break
                case 'ListForm':
                    this.selectModule('listActive')
                    break
                case 'ChargeList':
                    this.selectModule('chargeActive')
                    break
                case 'ChargeForm':
                    this.selectModule('chargeActive')
                    break
                case 'RemoteAgentsList':
                    this.selectModule('remoteAgentsActive')
                    break
                case 'CampaignList':
                    this.selectModule('campaignActive')
                    break
            }
        },
        selectModule(name){
            for(let index in this.actives){
                if(index == name){
                    this.actives[index] = true
                }else{
                    this.actives[index] = false
                }
            }
        }
    }
}